
.checkboxcontainer {
    position: relative;
    display: block;
    align-items: center;
    // margin-bottom: 10px;
    cursor: pointer;
    display: flex;
    .svg {
        fill: transparent;
        height: 16px;
        width: 16px;
    }
}
.sequre {
    display: inline-block;
    position: relative;
    margin-right: 8px;
    background-color: #fff;
    height: 15px;
    width: 15px;
    border-radius: 1px;
    border: solid 1px #d7d9db;
    transition: border-color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}

.checkboxInput {
    position: absolute;
    opacity: 0;
}

.banContainer {
    .svg {
        fill: transparent;
        width: 9px;
        height: 9px;
        background-color: #00b831;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-56%, -50%);
    }
}

.checkContainer {
    .sequre {
        background-color: #00b831;
        border-color: #00b831;
    }
    .svg {
        fill: #fff;
        width: 16px;
        height: 16px;
    }
}

.disableContainer {
    cursor: not-allowed;
    input {
        cursor: not-allowed;
    }
    .sequre {
        background-color: #f0f0f5;
        border: solid 1px #d7d9db;
    }
    .svg {
        fill: transparent;
       
    }
}

.disableContainer.banContainer {
    .sequre {
        background-color: #f0f0f5;
        border: solid 1px #d7d9db;
    }
    .svg {
        fill: transparent;
        background-color: #c0c0c4;
    }
}

.circle {
    .sequre {
        border-radius: 50%;
    }  
    .svg {
        border-radius: 50%;
    }
}

.horizontal {
    margin-right:18px;
}

.checkContainer.disableContainer {
   
.svg {
    fill: #93959e;
}
}
